<template>
    <div class="inner-box register" :loading='showLoading'>
        <div class="form-header">注册</div>
        <form class="form-body" id='formData'>
            <div class="row-input" :class='{"mistakeClasses": telMsg.length}'>
                <input v-model='formData.account' type="text" placeholder="手机号">
                <span>{{telMsg}}</span>
            </div>
            <div class="row-input" :class='{"mistakeClasses": psdMsg.length}'>
                <input v-model='formData.password' type="password" placeholder="密码">
                <span>{{psdMsg}}</span>
            </div>
            <div class="row-input" :class='{"mistakeClasses": repsdMsg.length}'>
                <input v-model='formData.repassword' type="password" placeholder="再次确认">
                <span>{{repsdMsg}}</span>
            </div>
            <div class="btn-row">
                <button type="button" class="btn" @click.stop='signUpAccount'>保存</button>
            </div>
        </form>
        <div class="form-footer">
            <button class="btn" @click='change'>已有账号</button>
        </div>
    </div>
</template>

<script>
import { setBase64 } from '@/utils/tools.js'
import {Ajax} from '@/api/index.js';
import Dialog from '@/components/loading';

export default {
    name: 'register-box',
    data() {
        return {
            formData: {
                account: '',
                password: '',
                repassword: ''
            },
          showLoading: false
        }
    },
    computed: {
      //判断手机号是否输入正确
      telMsg() {
        if(!this.formData.account) {
          return '账号不能为空'
        }

        let reg = /^[1][3,5,7,8,9][0-9]{9}$/;
        let isTrue = reg.test(this.formData.account);

        if(!isTrue) {
          return "输入账号有误"
        }

        return ""
      },
      //判断密码是否输入正确
      psdMsg() {
        if(!this.formData.password) {
          return '密码不能为空'
        }

        let reg = /^[A-Za-z0-9]{8,21}$/;
        let isTrue = reg.test(this.formData.password);

        if(!isTrue) {
          return "密码格式错误"
        }

        return ""

      },
      //两次密码错误提示信息
      repsdMsg() {
        if(!this.formData.repassword) {
          return '密码不能为空'
        }

        let isTrue = this.formData.password === this.formData.repassword;

        if(!isTrue) {
          return "两次密码输入不一致"
        }

        return ""

      }
    },
    methods: {
        // 切换到登录页面
        change() {
            this.$store.dispatch('changeLogin', false);
        },
        // 注册账号按钮
        signUpAccount() {
            let {account, password, repassword} = this.formData;

            // 再次判断输入框内的值
            if(!account || !password || !repassword) {
                this.telMsg = !account ? true : false;
                this.psdMsg = !password ? true : false;
                this.repsdMsg = !repassword ? true : false;
                return;
            }

            // 再次判断输入框内的值
            if(this.telMsg || this.psdMsg || this.repsdMsg) {
                Dialog.init({
                    type: 'warn',
                    message: "注册信息有误"
                })
                return;
            }
            this.showLoading = true;

            this.formData.password = setBase64(password)
            this.formData.repassword = setBase64(repassword)

            this.$store.dispatch("signUpUserInfo", this.formData).then(res => {
                let self = this;
                this.showLoading = false;

                Dialog.init({
                    type: 'success',
                    message: res.msg,
                    callback: function() {
                        self.$store.dispatch('changeLogin', false);
                    }
                })

            }).catch(error => {
                this.showLoading = false;

                Dialog.init({
                    type: 'error',
                    message: error.msg
                })
            })
        }
    }
}
</script>



